import { seo, pages } from "projectSettings"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import HomePageCover from "projectOrganisms/HomePageCover"
import * as helpers from "globalLibs/helpers"
import openGraphImageUrl from "projectSrc/assets/open-graph/default.jpg"

const PageStyle = styled.div`
  .title-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    .experience-info {
      color: ${({ theme }) => theme.tint_orange};
    }
  }
  .my-way-ul {
    overflow: hidden;
    li.active {
      :before {
        background-color: ${({ theme }) => theme.tint_green};
      }
      span {
        color: ${({ theme }) => theme.tint_green};
      }
    }
  }
`

export default function Resume(props) {
  return (
    <>
      <GatsbySeo
        title={pages.resume.title}
        description={pages.resume.description}
        canonical={seo.siteUrl}
        openGraph={{
          url: props.location.href,
          title: pages.resume.title,
          description: pages.resume.description,
          images: [
            {
              url: `${props.location.href}${openGraphImageUrl.replace(
                /\//,
                ""
              )}`,
              width: 1200,
              height: 630,
              alt: seo.siteSeoPrefix,
            },
          ],
        }}
      />
      <Layout>
        <section style={{ marginBottom: "128px" }}>
          <HomePageCover avatar>
            <span className="xxxLarge-high-title mb-16 fw-bold">
              Senior UX/UI Designer
            </span>
            <span className="xxxLarge-h1">Middle Web Developer</span>
          </HomePageCover>
        </section>
        <PageStyle
          as="article"
          className="container-fluid"
          style={{ maxWidth: "992px" }}
        >
          <div className="title-wrapper">
            <h1 className="mb-16">Resume</h1>
            <span className="experience-info mb-16">
              {helpers.calcExperience()} years of experience
            </span>
          </div>
          <hr className="mt-0"></hr>
          <h2>Personal information</h2>
          <ul>
            <li>Creative pseudonym - Artem Attvud</li>
            <li>Date of birth - 29th May 1992</li>
            <li>Nationality - Ukrainian</li>
            <li>
              Email - <a href="mailto:w@res.pm">w@res.pm</a>
            </li>
          </ul>
          <p>
            I have been working as a UX / UI designer since 2005. Since the end
            of 2018, in order to create and support his own projects, he began
            to learn programming. Currently I work as a Senior UX / UI designer
            (I only take on the most complex projects). I support my projects. I
            am learning the Python programming language.
          </p>
          <h2>Qualifications</h2>
          <ul>
            <li>Senior UX/UI Designer</li>
            <li>Middle Web Developer (Java Script, React, Node JS)</li>
          </ul>
          <h2>Achievements</h2>
          <ul>
            <li>
              developed a builder of design systems that can imitate any
              approach to create user interfaces (iOS, Android, etc.) or serve
              as a basis for creating a unique, maximally flexible design
              system. List of main features:
              <ul>
                <li>syncing with my own React component library.</li>
                <li>
                  optimization of work on routine tasks (UI Kit with fully
                  customizable components for any project, automatic export of
                  all icons in a project, all kinds of presets, etc.). As a
                  result, the time spent working on UX / UI is less than 400%
                  (!). The more complex the project, the more the optimization
                  feels.
                </li>
                <li>
                  automation of communication with developers. A repository on
                  GitHub is automatically created for each project. All project
                  data and files are stored in it. No one else needs to search
                  in messengers for links to design, sources, files with project
                  icons, etc. Everything in one place and available at any time.
                </li>
                <li>
                  semi-automatic branding. Automatic generation of identity
                  guidelines.
                </li>
                <li>
                  significant simplification of further project support.
                  Customers and programmers can request design changes with a
                  few clicks. History of changes is kept. As soon as I finish
                  work on the next edit or innovation, all project participants
                  receive a notification by mail.
                </li>
              </ul>
            </li>
            <li>successfully managed the work of a team of 12 designers.</li>
            <li>
              created UX/UI for niche leaders. Created UX/UI for products that
              later became niche leaders.
            </li>
            <li>
              I have supported and continue to support 3-4 high-load projects.
            </li>
            <li>
              learned to navigate perfectly in any approach to building user
              interfaces for the following platforms: iOS, Android, MacOS,
              Windows, Linux
            </li>
            <li>
              personally created my own projects from the very beginning to a
              fully working product (UX / UI, Front-end, Back-end). This site is
              also developed by me (Java Script, React, Node JS). You can at any
              time be convinced that its performance is significantly higher
              than the overwhelming majority of sites on the Internet.
            </li>
          </ul>
          <h2>Key skills</h2>
          <ul>
            <li>creation and support of the most complex design systems</li>
            <li>user experience research</li>
            <li>conversion optimization</li>
            <li>
              interaction with a team of designers and developers. Strict
              quality control
            </li>
          </ul>
          <h2>Work experience</h2>
          <ul>
            <li>
              2008 - 2015 - Received education. Work on freelance exchanges.
              Cooperation with Ukrainian and Russian web studios.
            </li>
            <li>
              2015 - 2016 - Worked in a Belarusian web studio as Middle UX/UI
              Designer.
            </li>
            <li>
              2016 - Established cooperation with regular customers and
              distanced himself from freelance exchanges.
            </li>
            <li>
              2017 - I have developed my own design system. Deepened his
              knowledge in the field of user behavior analysis and conversion
              optimization for e-commerce sites. Learned HTML5, CSS3. Began to
              be responsible for quality control of Front-end development on
              several projects. Started working for Upwork.
            </li>
            <li>
              2018 - Began to specialize in the most complex projects. Developed
              UX/UI for mapping services in Ukraine and Belarus. Perfectly
              mastered Google and Apple Guidlines. Added a lot of patterns to my
              design system for developing mobile app design. Continued to work
              for Upwork.
            </li>
            <li>
              2019 - Deepened my knowledge in Front-end development. I learned
              the Bottstrap framework, started learning Java Script, React and
              Node JS. I transferred my design system from Sketch to Figma,
              significantly expanded its capabilities and applications. Refused
              to work for Upwork in favor of working with regular clients.
            </li>
            <li>
              2020 -Continued to learn Java Script and its frameworks. I wrote a
              component library in React for my design system. Moved most of my
              projects to my own codebase. He continued to cooperate with
              regular customers.
            </li>
            <li>Started learning Python.</li>
          </ul>
          <h2>Personal qualities</h2>
          <ul>
            <li>Dependable</li>
            <li>Determined</li>
            <li>Initiative</li>
          </ul>
          <h2>Special skills</h2>
          <ul>
            <li>
              Language skills
              <ul>
                <li>Ukrainian - native</li>
                <li>Russian - native</li>
                <li>English - good reading and translating ability</li>
              </ul>
            </li>
            <li>
              Computer literacy
              <ul>
                <li>
                  UX/UI design - Figma, Sketch, Affinity Photo, Affinity
                  Designer
                </li>
                <li>Motion design - Adobe After Effects, Principle, Flinto</li>
                <li>Web Development - VC Code, WebStorm</li>
              </ul>
            </li>
          </ul>
          <h2>References</h2>
          <p>
            My recommendations are available on{" "}
            <a href="https://www.linkedin.com/in/Attvud" target="blank">
              my LinkedIn profile page
            </a>
          </p>
        </PageStyle>
      </Layout>
    </>
  )
}
